import {useContext, useRef} from "react"
import {SubmitHandler, useForm} from "react-hook-form"
import Button from "~/components/UI/Button/Button"
import InputText from "~/components/UI/InputText/InputText"
import RichTextField from "~/components/UI/RichText/RichText"
import AuthContext, {AllAttributes} from "~/contexts/AuthContext"
import {useAuthenticationLabelsQuery} from "~/queries/AuthenticationLabelsQuery"
import {classes} from "~/utilities/classes"
import {emailRegex, nameRegex, passwordRegex, phoneRegex} from "~/utilities/RegexStrings"
import TransitionContext from "../../../contexts/TransitionContext"
import {useLinksQuery} from "../../../queries/LinksQuery"
import "./AuthenticationForm.css"
import AuthenticationFormProps from "./AuthenticationFormProps"

type SignUpFields = {
    email: string
    password: string
    given_name: string
    family_name: string
    phone_number: string
    company_name: string | undefined
    company_type: string | undefined
    role: string | undefined
}

const SignUpForm: React.VFC<AuthenticationFormProps> = props => {
    const {className, children, disabled, setDisabled, edited, setEdited, onButtonClickCallback, ...otherProps} = props
    const signUpForm = useForm<SignUpFields>()
    const emailRef = useRef<HTMLDivElement>(null)
    const authContext = useContext(AuthContext)
    const labels = useAuthenticationLabelsQuery()
    const links = useLinksQuery()
    const transitionContext = useContext(TransitionContext)

    const onSignUpSubmitHandler: SubmitHandler<SignUpFields> = data => {
        setDisabled(true)
        const adkaorauser: AllAttributes = {
            given_name: data.given_name,
            family_name: data.family_name,
            phone_number: data.phone_number,
            company_name: data.company_name,
            company_type: data.company_type,
            role: data.role,
        }
        authContext.signUp(data.email, data.password, adkaorauser).then(result => {
            setEdited(false)
            setDisabled(false)
            if (onButtonClickCallback) onButtonClickCallback()
        })
    }

    return (
        <form
            autoComplete="off"
            className={classes("form-login-pt86", className)}
            onSubmit={signUpForm.handleSubmit(onSignUpSubmitHandler)}
            {...otherProps}
        >
            <RichTextField className="form-login-text-pt86">{labels.signUpMessage?.raw}</RichTextField>
            <div className="cell-pt86" ref={emailRef}>
                <InputText
                    disabled={disabled}
                    type="email"
                    placeholder={labels.email}
                    invalid={
                        signUpForm.formState.errors.email !== undefined ||
                        authContext.status === "SIGN_UP_ALREADY_REGISTERED"
                    }
                    {...signUpForm.register("email", {
                        required: true,
                        pattern: emailRegex,
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
                {authContext.status === "SIGN_UP_ALREADY_REGISTERED" && edited === false && (
                    <div className="messages-box-pt86">{labels.validationEmailAlreadyUsed}</div>
                )}
                {signUpForm.formState.errors.email !== undefined && (
                    <div className="messages-box-pt86">{labels.validationInvalidEmail}</div>
                )}
            </div>
            <div className="cell-pt86">
                <InputText
                    disabled={disabled}
                    type="password"
                    placeholder={labels.password}
                    invalid={signUpForm.formState.errors.password !== undefined}
                    {...signUpForm.register("password", {
                        required: true,
                        pattern: passwordRegex,
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
                {signUpForm.formState.errors.password !== undefined && (
                    <RichTextField className="messages-box-pt86">
                        {labels.validationInvalidPasswordSpecs?.raw}
                    </RichTextField>
                )}
            </div>
            <div className="cell-pt86">
                <InputText
                    disabled={disabled}
                    type="text"
                    placeholder={labels.name}
                    invalid={signUpForm.formState.errors.given_name !== undefined}
                    {...signUpForm.register("given_name", {
                        required: true,
                        pattern: nameRegex,
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
                {signUpForm.formState.errors.given_name !== undefined && (
                    <div className="messages-box-pt86">{labels.validationInvalidName}</div>
                )}
            </div>
            <div className="cell-pt86">
                <InputText
                    disabled={disabled}
                    type="text"
                    placeholder={labels.lastName}
                    invalid={signUpForm.formState.errors.family_name !== undefined}
                    {...signUpForm.register("family_name", {
                        required: true,
                        pattern: nameRegex,
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
                {signUpForm.formState.errors.family_name !== undefined && (
                    <div className="messages-box-pt86">{labels.validationInvalidLastName}</div>
                )}
            </div>
            <div className="cell-pt86">
                <InputText
                    disabled={disabled}
                    type="tel"
                    placeholder={labels.phoneNumber}
                    invalid={signUpForm.formState.errors.phone_number !== undefined}
                    {...signUpForm.register("phone_number", {
                        required: true,
                        pattern: phoneRegex,
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
                {signUpForm.formState.errors.phone_number !== undefined && (
                    <div className="messages-box-pt86">{labels.validationInvalidPhoneNumber}</div>
                )}
            </div>
            <div className="cell-pt86">
                <InputText
                    disabled={disabled}
                    type="text"
                    placeholder={labels.companyName}
                    invalid={signUpForm.formState.errors.company_name !== undefined}
                    {...signUpForm.register("company_name", {
                        required: false,
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
            </div>
            <div className="cell-pt86">
                <InputText
                    disabled={disabled}
                    type="text"
                    placeholder={labels.companyType}
                    invalid={signUpForm.formState.errors.company_type !== undefined}
                    {...signUpForm.register("company_type", {
                        required: false,
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
            </div>
            <div className="cell-pt86">
                <InputText
                    disabled={disabled}
                    type="text"
                    placeholder={labels.role}
                    invalid={signUpForm.formState.errors.role !== undefined}
                    {...signUpForm.register("role", {
                        required: false,
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
            </div>
            <Button className="button-pt86" type="submit" disabled={disabled} loading={disabled}>
                {labels.completeRegistrationButtonLabel}
            </Button>
        </form>
    )
}

export default SignUpForm
