import {useContext} from "react"
import {SubmitHandler, useForm} from "react-hook-form"
import Button from "~/components/UI/Button/Button"
import InputText from "~/components/UI/InputText/InputText"
import RichTextField from "~/components/UI/RichText/RichText"
import AuthContext, {AllAttributes} from "~/contexts/AuthContext"
import {useAuthenticationLabelsQuery} from "~/queries/AuthenticationLabelsQuery"
import {classes} from "~/utilities/classes"
import {nameRegex, phoneRegex} from "~/utilities/RegexStrings"
import "./AuthenticationForm.css"
import AuthenticationFormProps from "./AuthenticationFormProps"

type UpdatedAttributesFields = {
    given_name: string
    family_name: string
    phone_number: string
    company_name: string | undefined
    company_type: string | undefined
    role: string | undefined
}

const UpdatedAttributesForm: React.VFC<AuthenticationFormProps> = props => {
    const {className, children, disabled, setDisabled, edited, setEdited, onButtonClickCallback, ...otherProps} = props
    const updatedAttributesForm = useForm<UpdatedAttributesFields>()
    const authContext = useContext(AuthContext)
    const labels = useAuthenticationLabelsQuery()

    const onUpdatedAttributesSubmitHandler: SubmitHandler<UpdatedAttributesFields> = data => {
        setDisabled(true)
        const adkaorauser: AllAttributes = {
            given_name: data.given_name,
            family_name: data.family_name,
            phone_number: data.phone_number,
            company_name: data.company_name,
            company_type: data.company_type,
            role: data.role,
        }
        authContext.updateAttributes(adkaorauser).then(result => {
            setEdited(false)
            setDisabled(false)
            if (onButtonClickCallback) onButtonClickCallback()
        })
    }

    const switchToSignedIn = () => {
        authContext.setStatus("SIGNED_IN")
        if (onButtonClickCallback) onButtonClickCallback()
    }

    return (
        <form
            autoComplete="off"
            className={classes("form-login-pt86", className)}
            onSubmit={updatedAttributesForm.handleSubmit(onUpdatedAttributesSubmitHandler)}
            {...otherProps}
        >
            <RichTextField className="form-login-text-pt86">{labels.updateAttributesMessage?.raw}</RichTextField>
            <div className="cell-pt86">
                <InputText
                    disabled={disabled}
                    type="text"
                    placeholder={labels.name}
                    invalid={updatedAttributesForm.formState.errors.given_name !== undefined}
                    {...updatedAttributesForm.register("given_name", {
                        required: true,
                        pattern: nameRegex,
                        value: authContext.attributes?.given_name ? authContext.attributes?.given_name : "",
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
                {updatedAttributesForm.formState.errors.given_name !== undefined && (
                    <div className="messages-box-pt86">{labels.validationInvalidName}</div>
                )}
            </div>
            <div className="cell-pt86">
                <InputText
                    disabled={disabled}
                    type="text"
                    placeholder={labels.lastName}
                    invalid={updatedAttributesForm.formState.errors.family_name !== undefined}
                    {...updatedAttributesForm.register("family_name", {
                        required: true,
                        pattern: nameRegex,
                        value: authContext.attributes?.family_name ? authContext.attributes?.family_name : "",
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
                {updatedAttributesForm.formState.errors.family_name !== undefined && (
                    <div className="messages-box-pt86">{labels.validationInvalidLastName}</div>
                )}
            </div>
            <div className="cell-pt86">
                <InputText
                    disabled={disabled}
                    type="tel"
                    placeholder={labels.phoneNumber}
                    invalid={updatedAttributesForm.formState.errors.phone_number !== undefined}
                    {...updatedAttributesForm.register("phone_number", {
                        required: true,
                        pattern: phoneRegex,
                        value: authContext.attributes?.phone_number ? authContext.attributes?.phone_number : "",
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
                {updatedAttributesForm.formState.errors.phone_number !== undefined && (
                    <div className="messages-box-pt86">{labels.validationInvalidPhoneNumber}</div>
                )}
            </div>
            <div className="cell-pt86">
                <InputText
                    disabled={disabled}
                    type="text"
                    placeholder={labels.companyName}
                    invalid={updatedAttributesForm.formState.errors.company_name !== undefined}
                    {...updatedAttributesForm.register("company_name", {
                        required: false,
                        value: authContext.attributes?.company_name ? authContext.attributes?.company_name : "",
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
            </div>
            <div className="cell-pt86">
                <InputText
                    disabled={disabled}
                    type="text"
                    placeholder={labels.companyType}
                    invalid={updatedAttributesForm.formState.errors.company_type !== undefined}
                    {...updatedAttributesForm.register("company_type", {
                        required: false,
                        value: authContext.attributes?.company_type ? authContext.attributes?.company_type : "",
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
            </div>
            <div className="cell-pt86">
                <InputText
                    disabled={disabled}
                    type="text"
                    placeholder={labels.role}
                    invalid={updatedAttributesForm.formState.errors.role !== undefined}
                    {...updatedAttributesForm.register("role", {
                        required: false,
                        value: authContext.attributes?.role ? authContext.attributes?.role : "",
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
            </div>
            <Button className="button-pt86" type="submit" disabled={disabled} loading={disabled}>
                {labels.updateAttributesButtonLabel}
            </Button>
            <Button color="blue" className="button-pt86" type="button" disabled={disabled} onClick={switchToSignedIn}>
                {labels.cancelButtonLabel}
            </Button>
        </form>
    )
}

export default UpdatedAttributesForm
