import {useContext} from "react"
import Button from "~/components/UI/Button/Button"
import AuthContext from "~/contexts/AuthContext"
import CheckIcon from "~/icons/CheckIcon"
import ErrorIcon from "~/icons/ErrorIcon"
import {useAuthenticationLabelsQuery} from "~/queries/AuthenticationLabelsQuery"
import {classes} from "~/utilities/classes"
import {useLinksQuery} from "../../../queries/LinksQuery"
import GenericLink from "../../UI/GenericLink/GenericLink"

import "./UserPanel.css"

interface UserPanelProps extends React.HTMLAttributes<HTMLDivElement> {
    disabled: boolean
    setDisabled: (value: boolean) => void
    edited: boolean
    setEdited: (value: boolean) => void
}

const UserPanel = (props: UserPanelProps) => {
    const {className, children, disabled, setDisabled, edited, setEdited, ...otherProps} = props
    const authContext = useContext(AuthContext)
    const labels = useAuthenticationLabelsQuery()
    const links = useLinksQuery()

    const onSignOutClickHandler = (event: React.MouseEvent<HTMLElement>) => {
        setDisabled(true)
        authContext.signOut().then(() => {
            setDisabled(false)
        })
    }

    const onChangePasswordClickHandler = (event: React.MouseEvent<HTMLElement>) => {
        authContext.setStatus("CHANGE_PASSWORD")
    }

    const onUpdateAttributesClickHandler = (event: React.MouseEvent<HTMLElement>) => {
        authContext.setStatus("UPDATE_ATTRIBUTES")
    }
    const onDeleteAccountClickHandler = (event: React.MouseEvent<HTMLElement>) => {
        authContext.setStatus("DELETE_ACCOUNT")
    }

    return (
        <div className={classes("user-panel-container-g9dr", className)} {...otherProps}>
            {authContext.status === "SIGNED_IN_UPDATE_PASSWORD_SUCCESS" && (
                <div className="center-g9dr">
                    <CheckIcon className="check-icon-g9dr" />
                    <div className="success-label-g9dr">{labels.newPasswordSuccess}</div>
                </div>
            )}
            {authContext.status === "SIGNED_IN_UPDATE_PASSWORD_FAIL" && (
                <div className="center-g9dr">
                    <ErrorIcon className="error-icon-g9dr" />
                    <div className="success-label-g9dr">{labels.updateFailed}</div>
                </div>
            )}
            {authContext.status === "UPDATE_ATTRIBUTES_SUCCESS" && (
                <div className="center-g9dr">
                    <CheckIcon className="check-icon-g9dr" />
                    <div className="success-label-g9dr">{labels.attributesUpdateSuccess}</div>
                </div>
            )}
            {authContext.status === "UPDATE_ATTRIBUTES_FAIL" && (
                <div className="center-g9dr">
                    <ErrorIcon className="error-icon-g9dr" />
                    <div className="success-label-g9dr">{labels.updateFailed}</div>
                </div>
            )}
            {authContext.status === "SIGNED_IN" && (
                <div className="signed-in-message-g9dr">
                    {labels.privateAreaAccessMessage}
                    <GenericLink to={links.privateArea.url}>{links.privateArea.name}</GenericLink>
                </div>
            )}
            <div className="left-g9dr">
                {authContext.attributes?.email && <div>{authContext.attributes?.email}</div>}
                {authContext.attributes?.given_name && <div>{authContext.attributes?.given_name}</div>}
                {authContext.attributes?.family_name && <div>{authContext.attributes?.family_name}</div>}
                {authContext.attributes?.phone_number && <div>{authContext.attributes?.phone_number}</div>}
                {authContext.attributes?.company_name && <div>{authContext.attributes?.company_name}</div>}
                {authContext.attributes?.company_type && <div>{authContext.attributes?.company_type}</div>}
                {authContext.attributes?.role && <div>{authContext.attributes?.role}</div>}
            </div>
            <div className="right-g9dr">
                <Button onClick={onSignOutClickHandler} disabled={disabled}>
                    {labels.signOutButtonLabel}
                </Button>
                <Button disabled={disabled} onClick={onChangePasswordClickHandler}>
                    {labels.changePasswordButtonLabel}
                </Button>
                <Button disabled={disabled} onClick={onUpdateAttributesClickHandler}>
                    {labels.update}
                </Button>
                <Button color="red" onClick={onDeleteAccountClickHandler} disabled={disabled}>
                    {labels.deleteAccount}
                </Button>
            </div>
        </div>
    )
}

export default UserPanel
