import {useContext} from "react"
import {SubmitHandler, useForm} from "react-hook-form"
import Button from "~/components/UI/Button/Button"
import InputText from "~/components/UI/InputText/InputText"
import RichTextField from "~/components/UI/RichText/RichText"
import AuthContext from "~/contexts/AuthContext"
import {useAuthenticationLabelsQuery} from "~/queries/AuthenticationLabelsQuery"
import {classes} from "~/utilities/classes"
import {cognitoCodeRegex} from "~/utilities/RegexStrings"
import "./AuthenticationForm.css"
import AuthenticationFormProps from "./AuthenticationFormProps"

type ConfirmAccountFields = {
    confirmationCode: string
}

const ConfirmAccountForm: React.VFC<AuthenticationFormProps> = props => {
    const {className, children, disabled, setDisabled, edited, setEdited, onButtonClickCallback, ...otherProps} = props
    const confirmationForm = useForm<ConfirmAccountFields>()
    const authContext = useContext(AuthContext)
    const labels = useAuthenticationLabelsQuery()

    const onConfirmAccountSubmitHandler: SubmitHandler<ConfirmAccountFields> = data => {
        setDisabled(true)
        authContext.confirmAccount(data.confirmationCode).then(result => {
            setEdited(false)
            setDisabled(false)
            if (onButtonClickCallback) onButtonClickCallback()
        })
    }

    return (
        <form
            autoComplete="off"
            className={classes("form-login-pt86", className)}
            onSubmit={confirmationForm.handleSubmit(onConfirmAccountSubmitHandler)}
            {...otherProps}
        >
            <RichTextField className="form-login-text-pt86">{labels.confirmationMessage?.raw}</RichTextField>
            <div className="cell-pt86">
                <InputText
                    disabled={disabled}
                    type="text"
                    placeholder={labels.confirmationCode}
                    invalid={confirmationForm.formState.errors.confirmationCode !== undefined}
                    {...confirmationForm.register("confirmationCode", {
                        required: true,
                        pattern: cognitoCodeRegex,
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
                {confirmationForm.formState.errors.confirmationCode && (
                    <div className="messages-box-pt86">{labels.validationInvalidConfirmationCode}</div>
                )}
            </div>
            <Button className="button-pt86" type="submit" disabled={disabled} loading={disabled}>
                {labels.confirmButtonLabel}
            </Button>
        </form>
    )
}

export default ConfirmAccountForm
