import {useContext} from "react"
import {SubmitHandler, useForm} from "react-hook-form"
import Button from "~/components/UI/Button/Button"
import InputText from "~/components/UI/InputText/InputText"
import RichTextField from "~/components/UI/RichText/RichText"
import AuthContext from "~/contexts/AuthContext"
import {useAuthenticationLabelsQuery} from "~/queries/AuthenticationLabelsQuery"
import {classes} from "~/utilities/classes"
import {emailRegex} from "~/utilities/RegexStrings"
import "./AuthenticationForm.css"
import AuthenticationFormProps from "./AuthenticationFormProps"

type ForgotPasswordFields = {
    email: string
}

const ForgottenPasswordForm: React.VFC<AuthenticationFormProps> = props => {
    const {className, children, disabled, setDisabled, edited, onButtonClickCallback, setEdited, ...otherProps} = props
    const forgottenPasswordForm = useForm<ForgotPasswordFields>()
    const authContext = useContext(AuthContext)
    const labels = useAuthenticationLabelsQuery()

    const onForgottenPasswordSubmitHandler: SubmitHandler<ForgotPasswordFields> = data => {
        setDisabled(true)
        authContext.forgottenPassword(data.email).then(() => {
            setEdited(false)
            setDisabled(false)
            if (onButtonClickCallback) onButtonClickCallback()
        })
    }

    const switchToSignedOut = () => {
        authContext.setStatus("SIGNED_OUT")
        if (onButtonClickCallback) onButtonClickCallback()
    }

    return (
        <form
            autoComplete="on"
            className={classes("form-login-pt86", className)}
            onSubmit={forgottenPasswordForm.handleSubmit(onForgottenPasswordSubmitHandler)}
            {...otherProps}
        >
            <RichTextField className="form-login-text-pt86">{labels.forgottenPasswordMessage?.raw}</RichTextField>
            <div className="cell-pt86">
                <InputText
                    disabled={disabled}
                    type="email"
                    placeholder={labels.email}
                    invalid={
                        forgottenPasswordForm.formState.errors.email !== undefined ||
                        authContext.status === "FORGOTTEN_PASSWORD_INVALID"
                    }
                    {...forgottenPasswordForm.register("email", {
                        required: true,
                        pattern: emailRegex,
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
                {(forgottenPasswordForm.formState.errors.email !== undefined ||
                    authContext.status === "FORGOTTEN_PASSWORD_INVALID") && (
                    <div className="messages-box-pt86">{labels.validationInvalidEmail}</div>
                )}
            </div>
            <Button className="button-pt86" type="submit" disabled={disabled} loading={disabled}>
                OK
            </Button>
            <Button color="blue" className="button-pt86" type="button" disabled={disabled} onClick={switchToSignedOut}>
                {labels.cancelButtonLabel}
            </Button>
        </form>
    )
}

export default ForgottenPasswordForm
