import {documentToHtmlString} from "@contentful/rich-text-html-renderer"
import {classes} from "~/utilities/classes"
import "./RichText.css"

export interface RichTextFieldProps extends Omit<React.HTMLAttributes<HTMLDivElement>, "children"> {
    children?: any | undefined | null
}

const RichTextField: React.FC<RichTextFieldProps> = props => {
    const {children, className, ...otherPros} = props
    return (
        <div
            className={classes("rich-56aq", className)}
            dangerouslySetInnerHTML={
                children
                    ? {__html: documentToHtmlString(JSON.parse(children))}
                    : undefined
            }
            {...otherPros}
        />
    )
}

export default RichTextField
