import {useContext} from "react"
import {SubmitHandler, useForm} from "react-hook-form"
import Button from "~/components/UI/Button/Button"
import InputText from "~/components/UI/InputText/InputText"
import RichTextField from "~/components/UI/RichText/RichText"
import AuthContext from "~/contexts/AuthContext"
import {useAuthenticationLabelsQuery} from "~/queries/AuthenticationLabelsQuery"
import {classes} from "~/utilities/classes"
import {emailRegex} from "~/utilities/RegexStrings"
import "./AuthenticationForm.css"
import AuthenticationFormProps from "./AuthenticationFormProps"

type DeleteAccountFields = {
    email: string
}

const DeleteAccountForm: React.VFC<AuthenticationFormProps> = props => {
    const {className, children, disabled, setDisabled, edited, setEdited, onButtonClickCallback, ...otherProps} = props
    const deleteAccountForm = useForm<DeleteAccountFields>()
    const authContext = useContext(AuthContext)
    const labels = useAuthenticationLabelsQuery()

    const onDeleteAccountSubmitHandler: SubmitHandler<DeleteAccountFields> = data => {
        setDisabled(true)
        authContext.deleteAccount().then(result => {
            setEdited(false)
            setDisabled(false)
            if (onButtonClickCallback) onButtonClickCallback()
        })
    }

    const switchToSignedIn = () => {
        authContext.setStatus("SIGNED_IN")
        if (onButtonClickCallback) onButtonClickCallback()
    }

    return (
        <form
            autoComplete="off"
            className={classes("form-login-pt86", className)}
            onSubmit={deleteAccountForm.handleSubmit(onDeleteAccountSubmitHandler)}
            {...otherProps}
        >
            <RichTextField className="form-login-text-pt86">{labels.deleteAccountMessage?.raw}</RichTextField>
            <div className="cell-pt86">
                <InputText
                    disabled={disabled}
                    type="email"
                    placeholder={labels.email}
                    invalid={deleteAccountForm.formState.errors.email !== undefined}
                    {...deleteAccountForm.register("email", {
                        required: true,
                        validate: value => value === authContext.attributes?.email,
                        pattern: emailRegex,
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
                {deleteAccountForm.formState.errors.email && (
                    <div className="messages-box-pt86">{labels.validationInvalidEmail}</div>
                )}
            </div>
            <Button color="red" className="button-pt86" type="submit" disabled={disabled} loading={disabled}>
                {labels.confirmButtonLabel}
            </Button>
            <Button color="blue" className="button-pt86" type="button" disabled={disabled} onClick={switchToSignedIn}>
                {labels.cancelButtonLabel}
            </Button>
        </form>
    )
}

export default DeleteAccountForm
