import {classes} from "~/utilities/classes"
import "./MultiStyleText.css"

interface MultiStyleTextProps extends React.HTMLAttributes<HTMLSpanElement> {
    flex?: boolean | undefined
    children?: string | undefined | null
    className1?: string
    className2?: string
    style1?: React.CSSProperties | undefined
    style2?: React.CSSProperties | undefined
}

const MultiStyleText = (props: MultiStyleTextProps) => {
    const {className, className1, className2, flex = false, style1, style2, children: _children} = props
    const children = _children?.trim()

    if (!children) return null
    const numberOfChars = children.length
    const words = children.split(" ")
    const numberOfWords = words.length

    let part1 = ""
    let part2 = ""

    if (numberOfWords === 1) {
        part1 = children.substring(0, Math.round(children.length / 2))
        part2 = children.substring(Math.round(children.length / 2), children.length)
    } else {
        let half = 0
        for (let index = 1; numberOfChars / 2 - index > 0; index++) {
            half = children.indexOf(" ", numberOfChars / 2 - index)
            if (half !== -1) {
                break
            }
        }
        part1 = children.substring(0, half)
        part2 = children.substring(half, children.length)
    }

    return (
        <span className={classes("multi-style-vu88", flex ? "flex-vu88" : "grid-vu88", className)}>
            <span className={classes("part-1-vu88", numberOfWords > 1 ? "space-vu88" : "", className1)} style={style1}>
                {part1}
            </span>
            <span className={classes("part-2-vu88", numberOfWords > 1 ? "space-vu88" : "", className2)} style={style2}>
                {part2.trimEnd()}
            </span>
        </span>
    )
}

export default MultiStyleText
