import {PageProps} from "gatsby"
import {IGatsbyImageData} from "gatsby-plugin-image"
import {GatsbySeo} from "gatsby-plugin-next-seo"
import {Fragment, useContext, useRef, useState} from "react"
import ChangePasswordForm from "~/components/Forms/Authentication/ChangePasswordForm"
import ConfirmAccountForm from "~/components/Forms/Authentication/ConfirmAccountForm"
import ForgottenPasswordForm from "~/components/Forms/Authentication/ForgottenPasswordForm"
import ForgottenPasswordVerificationForm from "~/components/Forms/Authentication/ForgottenPasswordVerification"
import SignInForm from "~/components/Forms/Authentication/SignInForm"
import SignUpForm from "~/components/Forms/Authentication/SignUpForm"
import HeroSection from "~/components/Sections/HeroSection/HeroSection"
import UserPanel from "~/components/Sections/UserPanel/UserPanel"
import Button from "~/components/UI/Button/Button"
import MultiStyleText from "~/components/UI/MultiStyleText/MultiStyleText"
import RichTextField from "~/components/UI/RichText/RichText"
import ScrollButton from "~/components/UI/ScrollButton/ScrollButton"
import SectionContainer from "~/components/UI/SectionContainer/SectionContainer"
import AuthContext from "~/contexts/AuthContext"
import I18nContext from "~/contexts/I18nContext"
import {useAuthenticationLabelsQuery} from "~/queries/AuthenticationLabelsQuery"
import {useLinksQuery} from "~/queries/LinksQuery"
import DeleteAccountForm from "../components/Forms/Authentication/DeleteAccountForm"
import UpdatedAttributesForm from "../components/Forms/Authentication/UpdateAttributesForm"
import GenericLink from "../components/UI/GenericLink/GenericLink"
import CheckIcon from "../icons/CheckIcon"

import "./AuthenticationPageTemplate.css"

interface SignInPageProps extends PageProps<any, SignInPageContext> {}

const SignInPage: React.FC<SignInPageProps> = props => {
    const signInPageData = props.pageContext.data
    const [isEdited, setEdited] = useState(false)
    const [isDisabled, setDisabled] = useState(false)
    const containerRef = useRef<HTMLDivElement>(null)
    const authContext = useContext(AuthContext)
    const links = useLinksQuery()
    const labels = useAuthenticationLabelsQuery()
    const i18n = useContext(I18nContext)
    let timeoutRef = useRef<NodeJS.Timeout>()

    const signOut = () => {
        setDisabled(true)
        authContext.signOut().then(() => {
            setDisabled(false)
        })
    }

    const centerForm = () => {
        if (!containerRef.current) return
        containerRef.current.scrollIntoView({behavior: "smooth", block: "start"})
    }

    const switchToSignUp = () => {
        authContext.setStatus("SIGN_UP")
        timeoutRef.current = setTimeout(() => centerForm(), 10)
    }

    const switchToSignedIn = () => {
        authContext.setStatus("SIGNED_IN")
    }

    const switchToSignedOut = () => {
        authContext.setStatus("SIGNED_OUT")
        timeoutRef.current = setTimeout(() => centerForm(), 10)
    }

    return (
        <Fragment>
            <GatsbySeo
                title={`${signInPageData.title}`}
                canonical={`${process.env.SITE_URL}${links.authentication.url}`}
                language={i18n.language}
                openGraph={{
                    url: `${process.env.SITE_URL}${links.about.url}`,
                    title: `${signInPageData.title}`,
                    locale: i18n.language,
                    images: [
                        {
                            url: `${signInPageData.heroImage?.url}`,
                            width: Number(signInPageData.heroImage?.width),
                            height: Number(signInPageData.heroImage?.height),
                            alt: `${signInPageData.heroImage?.title}`,
                        },
                    ],
                }}
            />
            <HeroSection
                image={signInPageData.heroImage?.gatsbyImageData}
                imageAlt={signInPageData.heroImage?.title}
                mobileImage={signInPageData.heroImageMobile?.gatsbyImageData}
            >
                {authContext.status === "SIGNED_IN" && authContext.attributes ? (
                    <Fragment>
                        <h1 className="hero-title-part-3-d2bh">{labels.wellcome}</h1>
                        <h2 className="hero-title-part-4-d2bh">{`${authContext.attributes?.given_name} ${authContext.attributes?.family_name}`}</h2>
                    </Fragment>
                ) : (
                    <MultiStyleText flex className1="hero-title-part-1-d2bh" className2="hero-title-part-2-d2bh">
                        {signInPageData.title}
                    </MultiStyleText>
                )}
            </HeroSection>
            <SectionContainer tabbed marginPost customTab={<ScrollButton />}>
                <div className="authentication-container-8hxd" ref={containerRef}>
                    {authContext.status === "GENERIC_ERROR" && (
                        <RichTextField className="authentication-text-8hxd">{labels.unkownErrorMessage}</RichTextField>
                    )}
                    {authContext.status === "INVALID_USER_POOL" && (
                        <RichTextField className="authentication-text-8hxd">
                            {labels.cognitoErrorMessage?.raw}
                        </RichTextField>
                    )}
                    {(authContext.status === "SIGN_UP" ||
                        authContext.status === "SIGN_UP_INVALID" ||
                        authContext.status === "SIGN_UP_ALREADY_REGISTERED") && (
                        <SignUpForm
                            disabled={isDisabled}
                            setDisabled={setDisabled}
                            edited={isEdited}
                            setEdited={setEdited}
                            onButtonClickCallback={centerForm}
                        />
                    )}
                    {authContext.status === "CONFIRMATION_NECESSARY" && (
                        <ConfirmAccountForm
                            disabled={isDisabled}
                            setDisabled={setDisabled}
                            edited={isEdited}
                            setEdited={setEdited}
                            onButtonClickCallback={centerForm}
                        />
                    )}
                    {(authContext.status === "SIGNED_OUT" ||
                        authContext.status === "SIGNED_OUT_INVALID" ||
                        authContext.status === "SIGNED_OUT_UPDATE_PASSWORD_SUCCESS" ||
                        authContext.status === "SIGNED_OUT_UPDATE_PASSWORD_FAIL" ||
                        authContext.status === "SIGNED_OUT_ACCOUNT_CONFIRMED") && (
                        <SignInForm
                            disabled={isDisabled}
                            setDisabled={setDisabled}
                            edited={isEdited}
                            setEdited={setEdited}
                            onButtonClickCallback={centerForm}
                        />
                    )}
                    {authContext.status === "NEW_PASSWORD_REQUIRED" && (
                        <ConfirmAccountForm
                            disabled={isDisabled}
                            setDisabled={setDisabled}
                            edited={isEdited}
                            setEdited={setEdited}
                            onButtonClickCallback={centerForm}
                        />
                    )}
                    {authContext.status === "CHANGE_PASSWORD" && (
                        <ChangePasswordForm
                            disabled={isDisabled}
                            setDisabled={setDisabled}
                            edited={isEdited}
                            setEdited={setEdited}
                            onButtonClickCallback={centerForm}
                        />
                    )}
                    {(authContext.status === "FORGOTTEN_PASSWORD" ||
                        authContext.status === "FORGOTTEN_PASSWORD_INVALID") && (
                        <ForgottenPasswordForm
                            disabled={isDisabled}
                            setDisabled={setDisabled}
                            edited={isEdited}
                            setEdited={setEdited}
                            onButtonClickCallback={centerForm}
                        />
                    )}
                    {(authContext.status === "FORGOTTEN_PASSWORD_VERIFICATION" ||
                        authContext.status === "FORGOTTEN_PASSWORD_VERIFICATION_INVALID") && (
                        <ForgottenPasswordVerificationForm
                            disabled={isDisabled}
                            setDisabled={setDisabled}
                            edited={isEdited}
                            setEdited={setEdited}
                            onButtonClickCallback={centerForm}
                        />
                    )}
                    {authContext.status === "UPDATE_ATTRIBUTES" && (
                        <UpdatedAttributesForm
                            disabled={isDisabled}
                            setDisabled={setDisabled}
                            edited={isEdited}
                            setEdited={setEdited}
                            onButtonClickCallback={centerForm}
                        />
                    )}
                    {authContext.status === "DELETE_ACCOUNT" && (
                        <DeleteAccountForm
                            disabled={isDisabled}
                            setDisabled={setDisabled}
                            edited={isEdited}
                            setEdited={setEdited}
                            onButtonClickCallback={centerForm}
                        />
                    )}
                    {authContext.status === "DELETE_ACCOUNT_SUCCESS" && (
                        <div className="user-panel-container-g9dr">
                            <div className="center-g9dr">
                                <CheckIcon className="check-icon-g9dr" />
                                <div className="success-label-g9dr">
                                    {labels.accountDeleteSuccess}
                                    <GenericLink
                                        className="link-8hxd"
                                        to={links.homepage.url}
                                        onClick={() => authContext.resetStatus()}
                                    >
                                        {labels.backToTheHomepage}
                                    </GenericLink>
                                </div>
                            </div>
                        </div>
                    )}
                    {(authContext.status === "SIGNED_IN" ||
                        authContext.status === "SIGNED_IN_UPDATE_PASSWORD_SUCCESS" ||
                        authContext.status === "SIGNED_IN_UPDATE_PASSWORD_FAIL" ||
                        authContext.status === "UPDATE_ATTRIBUTES_SUCCESS" ||
                        authContext.status === "UPDATE_ATTRIBUTES_FAIL") && (
                        <Fragment>
                            <UserPanel
                                disabled={isDisabled}
                                setDisabled={setDisabled}
                                edited={isEdited}
                                setEdited={setEdited}
                            />
                        </Fragment>
                    )}
                </div>
                {(authContext.status === "SIGNED_OUT" || authContext.status === "SIGNED_OUT_INVALID") && (
                    <div className="switch-container-8hxd">
                        <span className="switch-label-8hxd">
                            {`${labels.missingAccount} `}
                            <Button color="link" onClick={switchToSignUp}>
                                {labels.clickHereLinkLabel}
                            </Button>
                        </span>
                    </div>
                )}
                {(authContext.status === "SIGN_UP" ||
                    authContext.status === "SIGN_UP_INVALID" ||
                    authContext.status === "SIGN_UP_ALREADY_REGISTERED") && (
                    <div className="switch-container-8hxd">
                        <span className="switch-label-8hxd">
                            {`${labels.alreadyHaveAccount} `}
                            <Button color="link" onClick={switchToSignedOut}>
                                {labels.clickHereLinkLabel}
                            </Button>
                        </span>
                    </div>
                )}
                {authContext.status === "CONFIRMATION_NECESSARY" ||
                    authContext.status === "FORGOTTEN_PASSWORD" ||
                    (authContext.status === "FORGOTTEN_PASSWORD_INVALID" && (
                        <div className="switch-container-8hxd">
                            <span className="switch-label-8hxd">
                                {`${labels.resendConfirmationCodeQuestion} `}
                                <Button color="link" onClick={() => authContext.resendConfirmationCode()}>
                                    {labels.resendConfirmationCodeLinkLabel}
                                </Button>
                            </span>
                        </div>
                    ))}
            </SectionContainer>
        </Fragment>
    )
}

export default SignInPage

interface SignInPageContext {
    data: {
        readonly node_locale: string
        readonly slug: string | null
        readonly title: string | null
        readonly heroImage: {
            readonly gatsbyImageData: IGatsbyImageData | null
            readonly url: string | null
            readonly width: number | null
            readonly height: number | null
            readonly title: string | null
        } | null
        readonly heroImageMobile: {
            readonly gatsbyImageData: IGatsbyImageData | null
            readonly url: string | null
            readonly width: number | null
            readonly height: number | null
            readonly title: string | null
        } | null
    }
}
