import {useContext} from "react"
import {SubmitHandler, useForm} from "react-hook-form"
import Button from "~/components/UI/Button/Button"
import InputText from "~/components/UI/InputText/InputText"
import RichTextField from "~/components/UI/RichText/RichText"
import AuthContext from "~/contexts/AuthContext"
import {useAuthenticationLabelsQuery} from "~/queries/AuthenticationLabelsQuery"
import {classes} from "~/utilities/classes"
import {passwordRegex} from "~/utilities/RegexStrings"
import "./AuthenticationForm.css"
import AuthenticationFormProps from "./AuthenticationFormProps"

type ChangePasswordFields = {
    password: string
    newPassword: string
}

const ChangePasswordForm: React.VFC<AuthenticationFormProps> = props => {
    const {className, children, disabled, setDisabled, edited, setEdited, onButtonClickCallback, ...otherProps} = props
    const changePasswordForm = useForm<ChangePasswordFields>()
    const authContext = useContext(AuthContext)
    const labels = useAuthenticationLabelsQuery()

    const onChangePasswordSubmitHandler: SubmitHandler<ChangePasswordFields> = data => {
        setDisabled(true)
        authContext.changePassword(data.password, data.newPassword).then(result => {
            setEdited(false)
            setDisabled(false)
            if (onButtonClickCallback) onButtonClickCallback()
        })
    }

    const switchToSignedIn = () => {
        authContext.setStatus("SIGNED_IN")
        if (onButtonClickCallback) onButtonClickCallback()
    }

    return (
        <form
            className={classes("form-login-pt86", className)}
            onSubmit={changePasswordForm.handleSubmit(onChangePasswordSubmitHandler)}
            {...otherProps}
        >
            <RichTextField className="form-login-text-pt86">{labels.passwordChangeMessage?.raw}</RichTextField>
            <div className="cell-pt86">
                <InputText
                    disabled={disabled}
                    type="password"
                    placeholder={labels.oldPassword}
                    invalid={changePasswordForm.formState.errors.password !== undefined}
                    {...changePasswordForm.register("password", {
                        required: true,
                        pattern: passwordRegex,
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
                {changePasswordForm.formState.errors.password !== undefined && (
                    <RichTextField className="messages-box-pt86">
                        {labels.validationInvalidPasswordSpecs?.raw}
                    </RichTextField>
                )}
            </div>
            <div className="cell-pt86">
                <InputText
                    disabled={disabled}
                    type="password"
                    placeholder={labels.newPassword}
                    invalid={changePasswordForm.formState.errors.newPassword !== undefined}
                    {...changePasswordForm.register("newPassword", {
                        required: true,
                        pattern: passwordRegex,
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
                {changePasswordForm.formState.errors.newPassword !== undefined && (
                    <RichTextField className="messages-box-pt86">
                        {labels.validationInvalidPasswordSpecs?.raw}
                    </RichTextField>
                )}
            </div>
            <Button className="button-pt86" type="submit" disabled={disabled} loading={disabled}>
                {labels.changePasswordButtonLabel}
            </Button>
            <Button color="blue" className="button-pt86" type="button" disabled={disabled} onClick={switchToSignedIn}>
                {labels.cancelButtonLabel}
            </Button>
        </form>
    )
}

export default ChangePasswordForm
