import {useContext} from "react"
import {SubmitHandler, useForm} from "react-hook-form"
import Button from "~/components/UI/Button/Button"
import InputText from "~/components/UI/InputText/InputText"
import RichTextField from "~/components/UI/RichText/RichText"
import AuthContext from "~/contexts/AuthContext"
import CheckIcon from "~/icons/CheckIcon"
import ErrorIcon from "~/icons/ErrorIcon"
import {useAuthenticationLabelsQuery} from "~/queries/AuthenticationLabelsQuery"
import {classes} from "~/utilities/classes"
import {emailRegex, passwordRegex} from "~/utilities/RegexStrings"
import "./AuthenticationForm.css"
import AuthenticationFormProps from "./AuthenticationFormProps"

type SignInFields = {
    email: string
    password: string
}

const SignInForm: React.VFC<AuthenticationFormProps> = props => {
    const {className, children, disabled, setDisabled, edited, setEdited, onButtonClickCallback, ...otherProps} = props
    const signInForm = useForm<SignInFields>()
    const authContext = useContext(AuthContext)
    const labels = useAuthenticationLabelsQuery()

    const onSignInSubmitHandler: SubmitHandler<SignInFields> = data => {
        setDisabled(true)
        authContext.signIn(data.email, data.password).then(result => {
            setDisabled(false)
            setEdited(false)
            // if (result === "SIGNED_IN") {
            //     window.scrollTo({top: 0, behavior: "smooth"})
            // }
            if (onButtonClickCallback) onButtonClickCallback()
        })
    }

    const switchToForgottenPassword = () => {
        authContext.setStatus("FORGOTTEN_PASSWORD")
        if (onButtonClickCallback) onButtonClickCallback()
    }

    return (
        <form
            autoComplete="on"
            className={classes("form-login-pt86", className)}
            onSubmit={signInForm.handleSubmit(onSignInSubmitHandler)}
            {...otherProps}
        >
            <RichTextField className="form-login-text-pt86">{labels.signInMessage?.raw}</RichTextField>
            {authContext.status === "SIGNED_OUT_UPDATE_PASSWORD_SUCCESS" && (
                <div className="center-pt86">
                    <CheckIcon className="check-icon-pt86" />
                    <div className="success-label-pt86">{labels.newPasswordSuccess}</div>
                </div>
            )}
            {authContext.status === "SIGNED_OUT_ACCOUNT_CONFIRMED" && (
                <div className="center-pt86">
                    <CheckIcon className="check-icon-pt86" />
                    <div className="success-label-pt86">{labels.confirmAccountSuccess}</div>
                </div>
            )}
            {authContext.status === "SIGNED_OUT_UPDATE_PASSWORD_FAIL" && (
                <div className="center-pt86">
                    <ErrorIcon className="error-icon-pt86" />
                    <div className="success-label-pt86">{labels.updateFailed}</div>
                </div>
            )}
            <div className="cell-pt86">
                <InputText
                    type="email"
                    placeholder={labels.email}
                    invalid={
                        signInForm.formState.errors.email !== undefined ||
                        (authContext.status === "SIGNED_OUT_INVALID" && edited === false)
                    }
                    {...signInForm.register("email", {
                        required: true,
                        pattern: emailRegex,
                        disabled: disabled,
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
                {signInForm.formState.errors.email && (
                    <div className="messages-box-pt86">{labels.validationInvalidEmail}</div>
                )}
            </div>
            <div className="cell-pt86">
                <InputText
                    type="password"
                    placeholder={labels.password}
                    invalid={
                        signInForm.formState.errors.password !== undefined ||
                        (authContext.status === "SIGNED_OUT_INVALID" && edited === false)
                    }
                    {...signInForm.register("password", {
                        required: true,
                        pattern: passwordRegex,
                        disabled: disabled,
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
                {signInForm.formState.errors.password !== undefined && (
                    <RichTextField className="messages-box-pt86">
                        {labels.validationInvalidPasswordSpecs?.raw}
                    </RichTextField>
                )}
                {authContext.status === "SIGNED_OUT_INVALID" && edited === false && (
                    <div className="messages-box-pt86">{labels.validationInvalidCredentials}</div>
                )}
                <div className="forgotten-password-pt86">
                    <Button color="link" onClick={switchToForgottenPassword}>
                        {labels.forgottenPasswordQuestion}
                    </Button>
                </div>
            </div>
            <Button className="button-pt86" type="submit" disabled={disabled} loading={disabled}>
                {labels.signInButtonLabel}
            </Button>
        </form>
    )
}

export default SignInForm
