import {useContext} from "react"
import {SubmitHandler, useForm} from "react-hook-form"
import Button from "~/components/UI/Button/Button"
import InputText from "~/components/UI/InputText/InputText"
import RichTextField from "~/components/UI/RichText/RichText"
import AuthContext from "~/contexts/AuthContext"
import {useAuthenticationLabelsQuery} from "~/queries/AuthenticationLabelsQuery"
import {classes} from "~/utilities/classes"
import {cognitoCodeRegex, passwordRegex} from "~/utilities/RegexStrings"
import "./AuthenticationForm.css"
import AuthenticationFormProps from "./AuthenticationFormProps"

type ForgottenPasswordVerificationFields = {
    verificationCode: string
    newPassword: string
}

const ForgottenPasswordVerificationForm: React.VFC<AuthenticationFormProps> = props => {
    const {className, children, disabled, setDisabled, edited, setEdited, onButtonClickCallback, ...otherProps} = props
    const forgottenPasswordVerificationForm = useForm<ForgottenPasswordVerificationFields>()
    const authContext = useContext(AuthContext)
    const labels = useAuthenticationLabelsQuery()

    const onForgottenPasswordVerificationSubmitHandler: SubmitHandler<ForgottenPasswordVerificationFields> = data => {
        setDisabled(true)
        authContext.forgottenPasswordConfirmNewPassword(data.verificationCode, data.newPassword).then(result => {
            setEdited(false)
            setDisabled(false)
            if (onButtonClickCallback) onButtonClickCallback()
        })
    }

    const switchToSignedOut = () => {
        authContext.setStatus("SIGNED_OUT")
        if (onButtonClickCallback) onButtonClickCallback()
    }

    return (
        <form
            autoComplete="off"
            className={classes("form-login-pt86", className)}
            onSubmit={forgottenPasswordVerificationForm.handleSubmit(onForgottenPasswordVerificationSubmitHandler)}
            {...otherProps}
        >
            <RichTextField className="form-login-text-pt86">
                {labels.forgottenPasswordVerificationMessage?.raw}
            </RichTextField>
            <div className="cell-pt86">
                <InputText
                    disabled={disabled}
                    type="number"
                    placeholder={labels.verificationCode}
                    invalid={
                        forgottenPasswordVerificationForm.formState.errors.verificationCode !== undefined ||
                        authContext.status === "FORGOTTEN_PASSWORD_VERIFICATION_INVALID"
                    }
                    {...forgottenPasswordVerificationForm.register("verificationCode", {
                        required: true,
                        pattern: cognitoCodeRegex,
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
                {(forgottenPasswordVerificationForm.formState.errors.verificationCode !== undefined ||
                    authContext.status === "FORGOTTEN_PASSWORD_VERIFICATION_INVALID") && (
                    <div className="messages-box-pt86">{labels.validationInvalidVerificationCode}</div>
                )}
            </div>
            <div className="cell-pt86">
                <InputText
                    disabled={disabled}
                    type="password"
                    placeholder={labels.newPassword}
                    invalid={forgottenPasswordVerificationForm.formState.errors.newPassword !== undefined}
                    {...forgottenPasswordVerificationForm.register("newPassword", {
                        required: true,
                        pattern: passwordRegex,
                    })}
                    containerClassName="form-login-input-pt86"
                    onKeyDown={() => setEdited(true)}
                />
                {forgottenPasswordVerificationForm.formState.errors.newPassword !== undefined && (
                    <RichTextField className="messages-box-pt86">
                        {labels.validationInvalidPasswordSpecs?.raw}
                    </RichTextField>
                )}
            </div>
            <Button className="button-pt86" type="submit" disabled={disabled} loading={disabled}>
                {labels.changePasswordButtonLabel}
            </Button>
            <Button color="blue" className="button-pt86" type="button" disabled={disabled} onClick={switchToSignedOut}>
                {labels.cancelButtonLabel}
            </Button>
        </form>
    )
}

export default ForgottenPasswordVerificationForm
